<template>
  <!-- 异地登录弹窗 -->
  <div class="shareType shareType15 maskBox" style="z-index: 2031">
    <!-- <div class="loginBox">
      <div class="msgContent">账号在另一个设备登录，请重新登录</div>
      <div class="confirmButton flex-row fr clear" @click="closePage">确定</div>
    </div> -->
    <div class="loginBox isIOS">
      <div class="title text-center">登录失效，请重新登录</div>
      <div class="button" @click="closePage">确定</div>
    </div>
  </div>
</template>
<script>
export default({
  data () {
    return {

    }
  },
  methods: {
    closePage () {
      this.$store.commit('common_other_login', false)
    }
  }
})
</script>

<style lang="scss" scoped>
// 异地登录
.shareType15 {
  .loginBox {
    background: #FFFFFF;
    border: 1px solid #979797;
    overflow: hidden;
    border-radius: 10px;
    width: 697px;
    padding: 30px 50px 0;
    box-sizing: border-box;
    .msgContent {
      font-size: 32px;
      color: #333;
      margin-top: 30px;
      margin-bottom: 36px;
    }
    .confirmButton {
      width: 165px;
      height: 62px;
      font-size: 34px;
      color:#fff;
      background: #ff7f18;
      border-radius: 31.5px;
      margin-bottom: 30px;
    }
  }
  .isIOS {
    width: 661px;
    padding: 0;
    .title{
      padding: 55px 0 42px;
      line-height: 45px;
    }
    .button {
      border-top: 1px solid #eeeeee;
      font-size: 34px;
      line-height: 48px;
      color: #ff7f18;
      text-align: center;
      padding: 22px 0 28px;
      font-weight: 900;
    }
  }
}
</style>



