import * as utils from '../assets/js/utils'
import {post} from '../assets/js/api'
import cooKies from 'vue-cookies'
let imageList = {}
if (utils.cache('imageList')) {
  imageList = JSON.parse(utils.cache('imageList'))
  // 大于七天清除数据
  if ((utils.getTimeStamp() - imageList.date) > (7 * 24 * 3600)) {
    utils.cache('imageList', null)
    imageList = {
      list: []
    }
  }
} else {
  imageList = {
    list: []
  }
}
let shareParams
if (utils.cache('shareParams')) {
  shareParams = JSON.parse(utils.cache('shareParams'))
} else {
  shareParams = null
}
export default {
  state: {
    backAction: 0,
    imgIndex: 0,
    isScroll: true,                    // 是否允许滚动
    login_bol: false,                  // 是否登录
    login_box: false,                  // 登录弹框控制值
    reload_address: '',                // token过期后地址
    index_column: false,               // 是否需要刷新频道
    protocol_bol: false,
    playUrl: ['https://2wk.com/vip.php?url=', 'https://cdn.yangju.vip/k/?url='],
    isLoading: 0,
    shares: {},                         // app分享对象
    inviteVcoin: '',                    // 邀请好友掘金宝
    showGuild: utils.cache('showGuild') ? 0 : true,                   // 是否需要显示任务引导
    noMessageTip: '没有更多数据了',      // 公共无数据提示
    inviteCode: utils.cache('inviteCode') ? utils.cache('invitecode') : '', // 协议
    recomd_channel: utils.cache('recomd_channel') ? JSON.parse(utils.cache('recomd_channel')) : [],
    my_channel: typeof utils.cache('my_channel') === 'string' ? JSON.parse(utils.cache('my_channel')) : [],
    pushInfo: {},
    oauthInfo: {},
    jsApiList: ['updateTimelineShareData', 'chooseImage', 'updateAppMessageShareData', 'onMenuShareAppMessage', 'onMenuShareTimeline', 'onMenuShareQQ', 'onMenuShareWeibo'],
    searchList: utils.cache('searchList') ? JSON.parse(utils.cache('searchList')) : [],
    userInfo: typeof utils.cache('userInfo') === 'string' ? JSON.parse(utils.cache('userInfo')) : {},
    updateData: {
      maintenance_time: '2018-10-30 21:04:04 到 2018-10-31 21:04:13',
      maintenance_info: '1、新增“小视频”频道，抖音、快手、火山视频全网打尽，一个app就能看个够！2、对部分算法进行优化调整；',
      vsn: '1.1.0'
    }, // 更新服务时的数据
    hasRed: {
      '/': false,
      '/movie': false,
      '/make_money': false,
      '/task': false,
      '/personal_center': false
    }, // 是否显示红点
    dbClick: 0,
    shareArc: {
      treasure_box: 10,
      inviting_friends: 11,
      benefits: 12,
      goods: 13,
      article: 14
    }, // 分享成文案
    imageList: imageList, // 随机图片
    columnid: 0, // 频道id
    columnidChange: 0, // 用于触发函数使用
    other_login: false,
    isDebug: false,      // 是否启动调试模式
    downloadChange: 0,
    shareParams,
    showTabbar: false,
    isBack: false
  },
  mutations: {
    // 设置是否是返回（返回则不调用原生的下一页方法）
    common_set_back (state, val) {
      state.isBack = val
    },
    common_hide_tabbar (state, val) {
      state.showTabbar = val
      console.log(state.showTabbar)
    },
    common_set_from (state, val) {
      state.appFrom = val
    },
    common_set_imgIndex (state, val) {
      state.imgIndex = val
    },
    // 设置分享参数
    setShareParams (state, val) {
      console.log(val, 'val')
      state.shareParams = val
      utils.cache('shareParams', JSON.stringify(val))
    },
    common_download_count (state) {
      state.downloadChange++
    },
    common_login_bol (state, val) {
      state.login_bol = val
    },
    common_login_box (state, val) {
      state.login_box = val
    },
    common_reload_address (state, val) {
      state.reload_address = val
    },
    common_index_column (state, val) {
      state.index_column = val
    },
    common_protocol_bol (state, val) {
      state.protocol_bol = val
    },
    common_inviteCode (state, val) {
      state.inviteCode = val
    },
    common_set_guild (state, val) {
      state.showGuild = val
    },
    common_set_shares (state, val) {
      state.shares = val
    },
    common_set_loading (state, val) {
      state.isLoading = val
    },
    common_set_backAction (state, val) {
      if (val === 0) {
        state.backAction = 0
      } else {
        state.backAction++
      }
    },
    /**
     *
     * @param {state} state
     * @param {*type为1 为删除 2为增加} params
     */
    common_set_channel (state, params) {
      let tempData = {}
      let data = {}
      // let urlName = ''
      if (params.type === 1) {
        tempData = JSON.parse(JSON.stringify(state.my_channel[params.index]))
        state.my_channel.splice(params.index, 1)
        data.channel_id = tempData.id
        // urlName = 'REMOVE_CHANNEL'
        state.recomd_channel.unshift(tempData)
      } else {
        tempData = JSON.parse(JSON.stringify(state.recomd_channel[params.index]))
        state.my_channel.push(tempData)
        data.channel_id = tempData.id
        // urlName = 'ADD_CHANNEL'
        state.recomd_channel.splice(params.index, 1)
      }
      if (state.login_bol) {
        // 已经登录的直接频道操作提交
        let arr = []
        for (let i in state.my_channel) {
          if (state.my_channel[i].id * 1 !== 0) {
            arr.push(state.my_channel[i].id)
          }
        }
        let data = {
          channel_id: arr.join(',')
        }
        post('ALL_SET', data)
      }
      utils.cache('recomd_channel', JSON.stringify(state.recomd_channel))
      utils.cache('my_channel', JSON.stringify(state.my_channel))
    },
    /**
     * 判断是否登录，登录了则设置设置我的频道
     * 未登录则设置八个为默认
     *
     */
    common_get_channel (state, channelData) {
      if (!cooKies.get('hasChannel')) {
        cooKies.set('hasChannel', true, '1d')
      }
      state.my_channel = channelData
      state.my_channel.unshift({
        name: '推荐',
        id: 0
      })
      utils.cache('my_channel', JSON.stringify(state.my_channel))
    },
    common_get_recondChannel (state, channelData) {
      if (!cooKies.get('hasChannel')) {
        cooKies.set('hasChannel', true, '1d')
      }
      state.recomd_channel = channelData
      utils.cache('recomd_channel', JSON.stringify(state.recomd_channel))
    },
    common_set_pushinfo (state, pushInfo) {
      state.pushInfo = pushInfo
    },
    common_set_userInfo (state, userInfo) {
      state.userInfo = userInfo
      utils.cache('userInfo', JSON.stringify(userInfo))
    },
    common_set_updateData (state, data) {
      state.updateData = data
    },
    common_set_hasRed (state, data) {
      state.hasRed = data
    },
    common_set_columnid (state, columnid) {
      state.columnid = columnid
    },
    common_set_columnid_change (state) {
      state.columnidChange++
    },
    common_other_login (state, bool) {
      state.other_login = bool
    },
    common_set_vcoin (state, vcoin) {
      state.inviteVcoin = vcoin
    },
    common_set_imageList (state, imageList) {
      state.imageList = imageList
      utils.cache('imageList', JSON.stringify(state.imageList))
    },
    common_oauthInfo (state, oauthInfo) {
      state.oauthInfo = oauthInfo
    },
    common_set_dobule (state, dbClick) {
      state.dbClick++
    },
    common_set_debug (state, bool) {
      state.isDebug = bool
    },
    common_set_scroll (state, bool) {
      state.isScroll = bool
    },
    common_set_searchList (state, {val, type}) {
      // type 为1时清除数据
      if (type === 1) {
        state.searchList = []
      } else {
        if (state.searchList.indexOf(val) === -1) {
          state.searchList.unshift(val)
        } else {
          if (!val) {
            return
          }
          state.searchList.splice(state.searchList.indexOf(val), 1)
          state.searchList.unshift(val)
        }
      }
      if (state.searchList.length > 10) {
        state.searchList = state.searchList.splice(0, 10)
      }
      utils.cache('searchList', JSON.stringify(state.searchList))
    }
  }
}



