import * as utils from '../assets/js/utils'
let playHistory = utils.cache('playHistory') ? JSON.parse(utils.cache('playHistory')) : []
export default {
  state: {
    playHistory: playHistory,
    searchHistory: utils.cache('searchHistory') ? JSON.parse(utils.cache('searchHistory')) : {history: []},
    movieBack: {count: 0, to: {}},
    movieActive: 0,
    showSearch: false,
    movieIndex: 0
  },
  mutations: {
    movie_setHistory (state, val) {           // {type: film, obj: {id: 电影视频ID, playTime: '播放时间'}} 每种类型有一个
      for (let [i, v] of state.playHistory) {
        if (v.id === val.id) {
          state.playHistory.splice(i, 1)
          break
        }
      }
      state.playHistory.unshift(val)
      if (state.playHistory.length > 150) {
        state.playHistory.pop()
      }
      utils.cache('playHistory', JSON.stringify(state.playHistory))
    },
    movie_removeHistory (state, val) {
      for (let [i, v] of state.playHistory) {
        if (v.id === val.id) {
          state.playHistory.splice(i, 1)
          break
        }
      }
      utils.cache('playHistory', JSON.stringify(state.playHistory))
    },
    movie_setSearchHistory (state, val) {
      // 如果关键词存在则将其提前
      let obj = Object.assign(state.searchHistory)
      if (val.type === 'clear') {
        obj = {
          history: []
        }
        state.searchHistory = obj
        utils.cache('searchHistory', JSON.stringify(obj))
        return
      }
      let historyArr = state.searchHistory.history ? Object.assign(state.searchHistory.history) : []
      if (state.searchHistory[val.name]) {
        historyArr.splice(historyArr.indexOf(val.name), 1)
        historyArr.unshift(val.name)
      } else {
        obj[val.name] = true
        historyArr.unshift(val.name)
        if (historyArr.length >= 30) {
          historyArr.pop()
        }
        obj.history = historyArr
      }
      state.searchHistory = obj
      utils.cache('searchHistory', JSON.stringify(obj))
    },
    movie_back (state, to) {
      state.movieBack.count++
      state.movieBack.to = to
    },
    movie_active (state) {
      state.movieActive++
    },
    movie_setShowSearch (state, val) {
      state.showSearch = val
    },
    // 共享当前激活的状态
    movie_setIndex (state, val) {
      state.movieIndex = val
    }
  }
}



