import axios from 'axios'
import qs from 'qs'
import config from './config'
import * as $utils from './utils'
import * as apiConstant from './api-constant'
import store from '../../store'
import {
  Indicator,
  Toast
} from 'mint-ui'
import router from '../../router'
axios.defaults.timeout = 15000
// axios.defaults.withCredentials = true
// 定义不需要自动关闭浮窗的接口
let urlArr = ['random_poster']
// axios.defaults.withCredentials = true
// 706 token失效错误码处理 微信登录随后处理
export const tokenFailure = () => {
  $utils.clearCache()
  if ($utils.isWx()) {
    if ($utils.cache('handOperated')) {
      store.commit('common_login_bol', false) // 登录状态变为未登录
      store.commit('common_login_box', true) // 跳出登录弹框
    } else {
      // 微信不弹框
      store.commit('common_login_box', false)
      store.commit('common_login_bol', false)
      // $utils.wxLogin()
    }
  } else {
    store.commit('common_login_bol', false) // 登录状态变为未登录
    store.commit('common_login_box', true) // 跳出登录弹框
  }
}
// 异地登录处理
export const OtherLogin = (res) => {
  // 非app不处理异地登录
  if (config.source_style >= 6) {
    $utils.clearCache()
    if ($utils.isWx()) {
      if ($utils.cache('handOperated')) {
        store.commit('common_login_bol', false) // 登录状态变为未登录
        store.commit('common_login_box', true) // 跳出登录弹框
      } else {
        $utils.wxLogin()
      }
    } else {
      store.commit('common_login_bol', false) // 登录状态变为未登录
      store.commit('common_login_box', true) // 跳出登录弹框
      store.commit('common_other_login', true)
    }
  } else {
    tokenFailure()
  }
}
// code 为3 时统一处理成弹框
export const msgAlert = (res) => {
  store.commit('set_shareSuccessObj', {
    type: 18,
    shareSuccessObj: {msg: res.data.msg}
  })
  store.commit('set_shareType', 18)
  store.commit('setShowShare', true)
}
/**
 * 
 * @param {string} constant url常量字符串
 * @param {object} data 参数对象
 * @param {function} callback 回调函数
 * @param {function} errorCallback 返回码不为0时的回调函数
 * @param {function} catchCallback 断网或网络问题时回调函数
 */
export const get = (constant, data = {}, callback, errorCallback, catchCallback) => {
  data.source_style = config.source_style
  data.uid = $utils.cache('uid') ? $utils.cache('uid') : 0
  data.user_token = $utils.cache('user_token') ? $utils.cache('user_token') : ''
  data.device_type = JSON.parse($utils.cache('phoneModel'))[1]
  data.version = config.version
  let url = apiConstant[constant]
  url = config.currentApi + url
  const timer = setTimeout(() => { // 五秒没有响应做的操作
    Indicator.close()
    Toast({
      message: '当前网络较慢,请检查网络',
      position: 'middle',
      duration: 1000
    })
  }, 20000)
  axios.get(
    url, {
      params: data
    }
  ).then((res) => {
    // 系统服务升级页面
    // res = {
    //   data: {
    //     data: {
    //       maintenance_time: '2018-10-30 21:04:04 到 2018-10-31 21:04:13',
    //       maintenance_info: '1、新增“小视频”频道，抖音、快手、火山视频全网打尽，一个app就能看个够！2、对部分算法进行优化调整；'
    //     },
    //     code: 707,
    //     msg: '系统升级',
    //     vsn: '1.2.3'
    //   }
    // }
    // 版本升级
    // res.data.vsn = '1.2.3'
    // 自定义alert弹窗用于code为3时候的消息
    // res = {
    //   data: {
    //     code: 3,
    //     msg: '测试code为3时默认标题'
    //   }
    // }
    // 判断当前接口是否需要关闭加载
    let flag = true
    for (let v of urlArr) {
      if (res.config.url.indexOf(v) > -1) {
        flag = false
        break
      }
    }
    if (flag) {
      Indicator.close()
    }
    clearTimeout(timer) // 清除定时器
    if ($utils.isUpdate(res.data.vsn ? res.data.vsn : config.version) === 1 && !$utils.cache('isShowUpdate')) {
      store.commit('setShowShare', true)
      store.commit('set_shareType', 8)
    }
    if (res.data.code === 706) {
      tokenFailure()
      return
      // 页面升级维护
    } else if (res.data.code === 705) {
      OtherLogin()
      return
    } else if (res.data.code === 707) {
      // 如果升级时间存在做处理
      if (res.data.data.maintenance_time) {
        store.commit('common_set_updateData', Object.assign({
          vsn: res.data.vsn
        }, res.data.data))
        router.push('/service_update')
        // location.href = '/#/service_update'
        return
      }
      // 错误状态码为2时不提示
    } else if (res.data.code === 2) {
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
    } else if (res.data.code === 3) {
      msgAlert(res)
      return
    } else if (res.data.code === 801 || res.data.code === 802 || res.data.code === 803) {
      if (res.data.code === 802) {
        if (config.source_style >= 6 || $utils.isWx()) {
          typeof errorCallback === 'function' && errorCallback(res.data)
        } else {
          store.commit('setShowShare', true)
          store.commit('set_shareType', 17)
          let shareSuccessObj = {
            
          }
          store.commit('set_shareSuccessObj', {
            type: 17,
            shareSuccessObj
          })
        }
      } else {
        typeof errorCallback === 'function' && errorCallback(res.data)
      }
      return
    } else if (res.data.code !== 0) {
      Toast({
        message: res.data.msg,
        position: 'middle',
        duration: 1000
      })
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
    }
    typeof callback === 'function' && callback(res.data)
  }).catch((error) => {
    typeof catchCallback === 'function' && catchCallback(error)
    Indicator.close()
  })
}

export const post = (constant, data = {}, callback, errorCallback, catchCallback) => {
  let str = `?source_style=${config.source_style}&uid=${$utils.cache('uid') ? $utils.cache('uid') : 0}&user_token=${$utils.cache('user_token') ? $utils.cache('user_token') : ''}&device_type=${JSON.parse($utils.cache('phoneModel'))[1]}&version=${config.version}`
  let url = apiConstant[constant] + str
  url = config.currentApi + url
  Indicator.open()
  const timer = setTimeout(() => { // 五秒没有响应做的操作
    Indicator.close()
    Toast({
      message: '当前网络较慢,请检查网络',
      position: 'middle',
      duration: 1000
    })
  }, 20000)
  axios.post(
    url,
    qs.stringify(data)
  ).then((res) => {
    clearTimeout(timer) // 清除定时器
    Indicator.close()
    if ($utils.isUpdate(res.data.vsn ? res.data.vsn : config.version) === 1 && !$utils.cache('isShowUpdate')) {
      store.commit('set_shareType', 8)
      store.commit('setShowShare', true)
    }
    if (res.data.code === 706) {
      tokenFailure()
      return
    } else if (res.data.code === 705) {
      OtherLogin()
      return
    } else if (res.data.code === 2) {
      return typeof errorCallback === 'function' && errorCallback(res.data)
    } else if (res.data.code === 3) {
      return msgAlert(res)
    } else if (res.data.code === 707) {
      // 如果升级时间存在做处理
      if (res.data.data.maintenance_time) {
        store.commit('common_set_updateData', Object.assign({
          vsn: res.data.vsn
        }, res.data.data))
        router.push('/service_update')
        // location.href = '/#/service_update'
        return
      }
      // 微信绑定 (该微信未绑定手机号)
    } else if (res.data.code === 801 || res.data.code === 802 || res.data.code === 803) {
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
      // 微信绑定 (该微信已绑定手机号)
    } else if (res.data.code === 802) {
      Toast({
        message: res.data.msg,
        position: 'middle',
        duration: 1000
      })
      typeof errorCallback === 'function' && errorCallback(res.data)
    } else if (res.data.code === 1) {
      if (res.config.url.indexOf('/wechat/oath') === -1) {
        Toast({
          message: res.data.msg,
          position: 'middle',
          duration: 1000
        })
        typeof errorCallback === 'function' && errorCallback(res.data)
      } else {
        if (data.type * 1 === 2) {
          typeof errorCallback === 'function' && errorCallback(res.data)
          Toast({
            message: res.data.msg,
            position: 'middle',
            duration: 1000
          })
        }
      }
      return
    } else if (res.data.code !== 0) {
      Toast({
        message: res.data.msg,
        position: 'middle',
        duration: 1000
      })
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
    }
    typeof callback === 'function' && callback(res.data)
  }).catch((error) => {
    typeof catchCallback === 'function' && catchCallback(error)
    // if (error)
    Toast({
      message: '',
      position: 'middle',
      duration: 1000
    })
    Indicator.close()
  })
}
/**
 * @param {string} url    请求完整地址
 * @param {object} data   请求传递参数
 * @param {function} successCallback 成功回调
 * @param {function} errorCallback 错误回调
 */

export const otherPost = (constant, data, successCallback, errorCallback) => {
  let url = apiConstant[constant]
  url = config.currentApi + url
  // Indicator.open({
  //   spinnerType: 'fading-circle'
  // })
  const timer = setTimeout(() => { // 五秒没有响应做的操作
    Indicator.close()
    Toast({
      message: '当前网络较慢,请检查网络',
      position: 'middle',
      duration: 1000
    })
  }, 20000)

  axios.get(`${url}?${qs.stringify(data)}`, data).then(res => {
    clearTimeout(timer) // 清除定时器
    Indicator.close()
    typeof successCallback === 'function' && successCallback(res.data)
  }).catch(err => {
    clearTimeout(timer) // 清除定时器
    Indicator.close()
    typeof errorCallback === 'function' && errorCallback(err)
  })
}

