export const postJson = (url, data, successCallback, errorCallback) => {
  let str = `?source_style=${config.source_style}&uid=${$utils.cache('uid') ? $utils.cache('uid') : 0}&user_token=${$utils.cache('user_token') ? $utils.cache('user_token') : ''}&device_type=${JSON.parse($utils.cache('phoneModel'))[1]}`
  url = apiConstant[url] + str
  url = config.currentApi + url
  Indicator.open()
  const timer = setTimeout(() => { // 五秒没有响应做的操作
    Indicator.close()
    Toast({
      message: '当前网络较慢,请检查网络',
      position: 'middle',
      duration: 1000
    })
  }, 20000)
  axios({
    url,
    data: JSON.stringify(data),
    headers: {
      'Content-Type': 'application/json'
    },
    method: 'POST'
  }).then((res) => {
    clearTimeout(timer)
    Indicator.close()
    if ($utils.isUpdate(res.data.vsn) === 1 && !$utils.cache('isShowUpdate')) {
      store.commit('setShowShare', true)
      store.commit('set_shareType', 8)
    }
    if (res.data.code === 706) {
      tokenFailure()
      return
    } else if (res.data.code === 705) {
      OtherLogin()
      return
    } else if (res.data.code === 707) {
      // 如果升级时间存在做处理
      if (res.data.data.maintenance_time) {
        store.commit('common_set_updateData', Object.assign({
          vsn: res.data.vsn
        }, res.data.data))
        router.push('/service_update')
        // location.href = '/#/service_update'
        return
      }
    } else if (res.data.code === 1) {
      return
    } else if (res.data.code !== 0) {
      Toast({
        message: res.data.msg,
        position: 'middle',
        duration: 1000
      })
      typeof errorCallback === 'function' && errorCallback(res.data)
      return
    }
    typeof successCallback === 'function' && successCallback(res.data)
  }).catch((err) => {
    clearTimeout(timer)
    Indicator.close()
    typeof errorCallback === 'function' && errorCallback(err)
  })
}
/**
 * 
 */
export const xhrGet = (url, data, cb, errCb) => {
  url = `http://is.snssdk.com${url}?${qs.stringify(data)}`
  Indicator.open()
  const timer = setTimeout(() => { // 五秒没有响应做的操作
    Indicator.close()
    Toast({
      message: '当前网络较慢,请检查网络',
      position: 'middle',
      duration: 1000
    })
  }, 20000)
  let xhr
  if (window.XMLHttpRequest) {
    // code for IE7+, Firefox, Chrome, Opera, Safari
    xhr = new XMLHttpRequest()
  } else { // code for IE6, IE5
    xhr = new window.ActiveXObject('Microsoft.XMLHTTP')
  }
  xhr.open('GET', url, true)
  xhr.onreadystatechange = function () {
    if (xhr.readyState === 4 && xhr.status === 200) {
      if (xhr.status === 200) {
        cb && cb(JSON.parse(xhr.responseText))
      } else {
        errCb && errCb({code: 2, message: '代理错误'})
      }
      clearInterval(timer)
    }
  }
  xhr.send(data)
}

let baseUrl = '/movie'
export const testApi = (url, data, cb) => {
  url = baseUrl + url
  axios.get(`${url}?${qs.stringify(data)}`).then(res => {
    if (res.data.code === 0) {
      cb && cb(res.data)
    } else {
      Toast({
        message: res.data.msg
      })
    }
  })
}



