<template>
  <div id="download" class="maskBox" style="z-index: 1600">
    <div class="content">
      <div class="title">{{isComplete ? '下载已完成' : '正在下载，请耐心等待'}}</div>
      <div class="wrapper" v-if="!isComplete">
        <div class="inner" :style="{width: progress ? progress + '%' : 0}"></div>
      </div>
      <div class="percent" v-if="!isComplete">{{isComplete ? '下载已完成' : `进度${progress}%`}}</div>
      <div class="buttons" v-if="isComplete">
        <div class="confirm" @click="toInstall">立即安装</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    progress: {
      type: Number,
      default: 0
    },
    isComplete: {
      type: Boolean
    }
  },
  methods: {
    toInstall () {
      this.$emit('toInstall')
    }
  }
}
</script>
<style lang="scss" scoped>
  #download {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba($color: #000000, $alpha: .5);
    .content {
      width: 600px;
      padding: 50px 50px 0px;
      box-sizing: border-box;
      border-radius: 10px;
      overflow: hidden;
      .title {
        font-size: 36px;
        // font-weight: 900;
        text-align: center;
        line-height: 45px;
        padding-bottom: 20px;
      }
      .wrapper {
        width: 100%;
        border-radius: 30px;
        margin: 0 auto;
        overflow: hidden;
        height: 30px;
        background: #dadada;
        .inner {
          width: 20%;
          height: 40px;
          background: #F76F51;
        }
      }
      .percent {
        font-size: 32px;
        line-height: 45px;
        margin-top: 20px;
        text-align: center;
        color: #999;
        margin-bottom: 50px;
      }
    }
    .buttons {
      text-align: center;
      padding: 30px 0;
      .confirm {
        width: 240px;
        height: 80px;
        font-size: 32px;
        line-height: 45px;
        background: #F76F51;
        color: #fff;
        margin: 0 auto;
        border-radius: 50px;
        line-height: 80px;
      }
    }
  }
</style>



