import 'babel-polyfill'
import Vue from 'vue'
import vuex from 'vuex'

import common from './common'
import comment from './comment'
import share from './share'
import movie from './movie'

Vue.use(vuex)

export default new vuex.Store({
  modules: {
    common,       // 全局公共
    comment,       // 评论数据
    share,         // 分享数据
    movie
  }
})



