import * as utils from '../assets/js/utils'
export default {
  state: {
    videoList: utils.cache('videoList') ? JSON.parse(utils.cache('videoList')) : [], // 视频列表
    videoNav: [],  // 视频分类
    currentDetail: utils.cache('currentDetail') ? JSON.parse(utils.cache('currentDetail')) : {}, // 点击后当前视频
    currentSmallVideo: utils.cache('currentSmallVideo') ? JSON.parse(utils.cache('currentSmallVideo')) : {}, // 小视频列表
    smallVideoList: utils.cache('smallVideoList') ? JSON.parse(utils.cache('smallVideoList')) : {}, // 当前小视频
    smllVideoIndex: utils.cache('smllVideoIndex') ? utils.cache('smllVideoIndex') : 0, // 当前小视频下标
    user_city: utils.cache('user_city') ? utils.cache('user_city') : ''
  },
  mutations: {
    // 设置视频列表
    comment_video_list (state, videoList) {
      state.videoList = videoList
      utils.cache('videoList', JSON.stringify(videoList))
    },
    // 设置当前视频详情
    comment_video_detail (state, detail) {
      state.currentDetail = detail
      utils.cache('currentDetail', JSON.stringify(detail))
    },
    // 设置当前小视频列表
    comment_get_small_videolist (state, smallVideoList) {
      state.smallVideoList = smallVideoList
      utils.cache('smallVideoList', JSON.stringify(smallVideoList))
    },
    // 设置当前小视频下标
    comment_set_small_index (state, smllVideoIndex) {
      state.smllVideoIndex = smllVideoIndex
      utils.cache('smllVideoIndex', smllVideoIndex)
    },
    // 设置当前小视频详情
    comment_get_small_video (state, currentSmallVideo) {
      state.currentSmallVideo = currentSmallVideo
      utils.cache('currentSmallVideo', JSON.stringify(currentSmallVideo))
    },
    // 设置用户城市
    comment_set_city (state, userCity) {
      state.user_city = userCity
    }
  }
}



