<template>
  <div id='topMessage' @click="setMessage" class="topMessage flex-row" style="z-index: 9">
    <div class="leftImage">
      <img v-if="messageObj.image && messageObj.image.length > 0" :src="messageObj.image[0]" alt="图片">
      <img v-else src="../../../assets/img/logo.png" alt="logo">
    </div>
    <div class="rightContent">
      <div class="title">{{messageObj.title}}</div>
      <div class="content" v-text="messageObj.content"></div>
    </div>
    <div class="line" @click.stop="closeMessage"></div>
  </div>
</template>
<script>
export default {
  props: {
    messageObj: {
      type: Object,
      default () {
        return {
          title: '清除缓存',
          content: ''
        }
      }
    }
  },
  data () {
    return {

    }
  },
  methods: {
    closeMessage () {
      this.$emit('closeMessage')
    },
    setMessage () {
      let data = {
        msg_id: this.messageObj.id
      }
      // 统一跳转消息页面
      this.$router.push('/my/message')
      this.closeMessage()
      this.$api.get('MESSAGE_READ', data, res => {})
    }
  },
  mounted () {
    setTimeout(() => {
      this.closeMessage()
    }, 3000)
  },
  watch: {
    $route (path) {
      var arr = ['/', '/make_money', 'task', '/personal_center']
      let flag = false
      for (let v of arr) {
        if (path === v) {
          flag = true
          break
        }
      }
      if (!flag) {
        this.closeMessage()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.topMessage {
  position: fixed;
  // display: none;
  padding: 0 30px;
  left: 0;
  right: 0;
  top: 0;
  height: 160px;
  background: rgba(0, 0, 0, .85);
  .leftImage {
    height: 80px;
    margin-right: 20px;
    overflow: hidden;
    width: 80px;
    text-align: center;
    border-radius: 50%;
    position: relative;
    img {
      height: 100%;
      display: block;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .rightContent {
    color: #fff;
    text-align: left;
    flex: 1;
    min-width: 0;
    .title {
      font-size: 32px;
      line-height: 45px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .content {
      display: block;
      line-height: 40px;
      white-space: nowrap;
      font-size: 28px;
      margin-top: 6px;
      text-overflow: ellipsis;
      max-width: 100%;
      overflow: hidden;
      height: 40px;
    }
  }
  .line {
    position: absolute;
    bottom: 0;
    left: 50%;
    height: 40px;
    width: 100px;
    transform: translateX(-50%);
    &::after{
      display: block;
      content: '';
      width: 50px;
      height: 4px;
      background: #eee;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
</style>



