// import store from '../store'
import * as $utils from '../assets/js/utils'
import store from '../store/index'

/**
 * @method 这是路由钩子函数
 * @param { Object } to   到哪里去的路由对象
 * @param { Object } from 从哪里来的路由对象
 * @param { Function } next 路由钩子函数
 */
export default (to, from, next) => {
  console.log(to, from)
  if (to.query.inviteCode) {
    $utils.cache('inviteCodeUser', to.query.inviteCode)
  }
  if (to.meta.title !== '文章详情' && to.meta.title !== '媒体号') {
    document.title = to.meta.title
  }
  // 与原生交互进行显示隐藏
  if (!store.state.common.showTabbar) {
    if (to.path === '/task' || to.path === '/' || to.path === '/personal_center' || to.path === '/movie' || to.path === '/make_money') {
      next()
      if (to.path === '/personal_center') {
        $utils.plusReady(plus => {
          plus.myplugin && plus.myplugin.goNativeView(from.path, to.path)
        })
      }
     
      window.plus && window.plus.myplugin && window.plus.myplugin.plgBack()
    } else {
      if (!store.state.common.isBack) {
        $utils.plusReady(plus => {
          plus.myplugin && typeof plus.myplugin.plgNextPage === 'function' && plus.myplugin.plgNextPage(from.path, to.meta.title, null)
        })
        next()
      } else {
        next()
      }
      store.commit('common_set_back', false)
    }
  } else {
    next()
  }
}



