const uniqueNum = (prefix) => {
  return (prefix || '') + new Date().getTime().toString(36) + Math.random().toString(36).slice(2)
}
let variable = {  // 这个是初始默认值
  vsn: '/v1',
  source_style: 6,
  package_app_env: '0',
  package_brower: '1',
  product: true,
  ch_rand: uniqueNum(),
  version: '1.8.1',
  devApi: 'http://dev.api.juejinchain.cn', // 测试服务器api http://juejin.wechat-app.club
  // devApi: 'http://.m.juejinchain.com', // 测试服务器api http://juejin.wechat-app.club
  // devApi: 'http://192.168.0.21:8002', // 测试服务器api http://juejin.wechat-app.club
  // productApi: 'http://api.juejinchain.com', // 线上服务器api
  productApi: 'http://api.juejinchain.com', // 线上服务器api
  currentApi: 'http://dev.api.juejinchain.cn', // 实际使用api
  // currentApi: 'http://api.juejinchain.com', // 实际使用api http://192.168.0.18:8002/v1/passport/captcha?timestamp=1545642504578
  // currentApi: 'http://192.168.0.18:8002',
  devWebUrl: 'http://dev.wechat.juejinchain.cn', // 测试服务器分享出去的域名
  productWebUrl: 'http://wechat.jjbnews.top', // 线上服务器分享出去的域名
  currentWebUrl: 'http://wechat.jjbnews.top', // 当前分享出去的地址
  packageTime: '',
  downloadUrl: 'http://wechat.jjbnews.top/download/'
}

if (process.env.NODE_ENV === 'pcEnv') {   // 各开发环境下的参数变化
  // pc端
  variable.source_style = 1
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'mobileEnv') {
  // Mobile端
  variable.source_style = 2
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'wechatEnv') {
  // 微信公众号端
  variable.source_style = 3
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'appletsEnv') {
  // 小程序端
  variable.source_style = 4
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'promotionEnv') {
  // 推广端
  variable.source_style = 5
  variable.currentApi = '/api'
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'iosEnv') {
  // IOS端
  variable.source_style = 6
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = false
  variable.currentApi = variable.devApi
  variable.currentWebUrl = variable.devWebUrl
  variable.packageTime = new Date()
} else if (process.env.NODE_ENV === 'androidEnv') {
  // 安卓端
  variable.source_style = 7
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = false
  variable.currentApi = variable.devApi
  variable.currentWebUrl = variable.devWebUrl
  variable.packageTime = new Date()
  // iOS端正式服务器
} else if (process.env.NODE_ENV === 'iosproductEnv') {
  variable.source_style = 6
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = true
  variable.currentApi = variable.productApi
  variable.currentWebUrl = variable.productWebUrl
  // 安卓正式服务器
} else if (process.env.NODE_ENV === 'androidproductEnv') {
  variable.source_style = 7
  variable.package_app_env = '1'
  variable.package_brower = '0'
  variable.product = true
  variable.currentApi = variable.productApi
  variable.currentWebUrl = variable.productWebUrl
} else {
  // 没有打包时的环境
  variable.product = false
  if (variable.source_style < 6) {
    variable.currentApi = '/api'
  }
}
export default {
  vsn: variable.vsn,         // 版本号
  /* [
       '1' => 'PC',
       '2' => 'Mobile',
       '3' => '微信公众号',
       '4' => '小程序',
       '5' => '推广',
       '6' => 'IOS',
       '7' => '安卓',
     ] */
  source_style: variable.source_style,    // 根据不同客户端打包的时候参数
  // 在打包前，一定要手动修改以下两个变量的值，如果是打包成APP，那么就让package_app_env的值为1，否则为0，如果不是打包成APP，就让package_brower的值为1，否则为0
  package_app_env: variable.package_app_env, // 表示是APP环境
  package_brower: variable.package_brower, // 表示浏览器环境
  product: variable.product, // true 表示线上环境，false表示测试环境
  ch_rand: variable.ch_rand,  // 区别用户
  devApi: variable.devApi, // 测试服务器api
  productApi: variable.productApi, // 线上服务器api
  currentApi: variable.currentApi, // 实际使用api
  devWebUrl: variable.devWebUrl, // 测试服务器分享出去的域名
  productWebUrl: variable.productWebUrl, // 线上服务器分享出去的域名
  currentWebUrl: variable.currentWebUrl, // 当前分享域名供app使用
  downloadUrl: variable.downloadUrl,
  // androidDownloadUrl: 'https://sj.qq.com/myapp/detail.htm?apkName=com.juejinchain.android', // 应用市场下载地址
  androidDownloadUrl: variable.downloadUrl, // 应用市场下载地址
  iOSDownloadUrl: 'https://sj.qq.com/myapp/detail.htm?apkName=com.juejinchain.android', // iOS下载地址
  version: variable.version,
  packageTime: variable.packageTime
}



