import * as utils from '../assets/js/utils'
export default {
  state: {
    showSuccessShare: false, // 显示分享成功提示框
    shareType: 8, // 弹框类型 1微信分享完成加速弹框 2 新读者打开自动赚钱 3 分享大于5次显示弹窗  4 分享这篇文章赚取豪车 5 qq分享完成弹框 6 领取豪车
    shareCount: 1, // 分享次数 不管是否成功
    shareSuccessObj1: {  // 分享对象数据
      baseRate: 0.5, // 基础倍率
      makeRate: 1
    },
    shareSuccessObj2: { // 分享数据类型2
      hasGet: 0.00, // 当前已赚
      makeRate: 0 // 赚钱速率
    },
    shareSuccessObj3: {
      qrLink: 'http://qr.api.cli.im/qr?data=http%253A%252F%252Fmp.weixin.qq.com%252Fmp%252Fgetmasssendmsg%253F__biz%253DMzU2NjY5NTU1NQ%253D%253D%2523wechat_webview_type%253D1%2526wechat_redirect&level=H&transparent=false&bgcolor=%23ffffff&forecolor=%23000000&blockpixel=12&marginblock=1&logourl=%2F%2Foss-cn-hangzhou.aliyuncs.com%2Fpublic-cli%2Ffree%2Fbc43e66d6cffdedae35ed070c74fc4761537869728.png&size=260&kid=cliim&key=a10a4c82246c3a75f41a5c017740ccb3',
      hasGet: 0.00, // 当前已赚
      makeRate: 0 // 赚钱速率
    },
    shareSuccessObj4: { // 车的数量
      carImg: require('../assets/img/carImg.png'),
      carIcon: require('../assets/img/carIcon.png'),
      carName: '2018款 730Li 领先型 M运动套装',
      nickname: '花在风雨中',
      avatar: 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png',
      carPrice: '82.88',
      joinNum: 2312123
    },
    shareSuccessObj7: {
      coin: 50,
      extra_coin: 15,
      type: 16
    },
    // 登录数据
    shareSuccessObj15: {
      coin: 100,
      type: 12
    },
    shareSuccessObj16: {
      // coin: 100,
      // type: 1,
      // extra_coin: 30,
      // tipText: '用掘金宝App读文章能多赚更多金币！'
    },
    shareSuccessObj17: {
      
    },
    shareSuccessObj18: {
      msg: ''
    },
    // 分享到qq弹窗所需要的数据
    shareSuccessObj5: {
      remainTime: 740,   // 剩余秒数
      remainNum: 13   // 剩余人数
    },
    // 立即领取弹窗
    shareSuccessObj6: {
      carName: '宝马320Li',
      carImg: require('../assets/img/carImg.png'),
      carIcon: require('../assets/img/carIcon.png'),
      tip: '即将把豪车领回家，你也赶紧试试！',
      nickname: '花在风雨中',
      avatar: 'http://thirdwx.qlogo.cn/mmopen/vi_32/Q0j4TwGTfTIdmxUuslFdVicFQZsJBQLdc2e8z3WYzYjaVVjiamR4icLpAAUsTibPAONiaboY00ZytibYAfmyiaYmEcmtg/132',
      bottomTip: '掘金宝新用户专享',
      joinNum: 25456,
      hasGet: false
    },
    shareSuccessObj8: {}, // 升级弹框参数
    shareSuccessObj9: {}, // 大礼包弹框参数
    shareSuccessObj10: {}, // 大礼包成功
    shareSuccessObj11: {}, // 等级升级
    shareSuccessObj12: {
      price: '19.00',
      shortname: '小米 随身wifi',
      thumb: 'http://jjlmobile.oss-cn-shenzhen.aliyuncs.com/goods_jd/gid_12157971534/pic_list_h/f16fb4d5d00f6ca18ae928977e97c1a4.jpg?x-oss-process=image/resize,h_100',
      user_count: 124545
    }, // 实物等级升级
    shareSuccessObj13: {
      pushTitle: '欢迎进入掘金平台看资讯',
      pushContent: '点击领取洋房、豪车、现金大礼包100%中奖，看你手气点击领取洋房、豪车、现金大礼包100%中奖，看你手气点击领取洋房、豪车、看你手气点击领取洋房、豪车、看你手气点击领取洋房、豪车、现金大礼包…100%中奖，看你手气点击领取洋房',
      image: 'http://jjlmobile.oss-cn-shenzhen.aliyuncs.com/goods_jd/gid_12157971534/pic_list_h/f16fb4d5d00f6ca18ae928977e97c1a4.jpg?x-oss-process=image/resize,h_200',
      url: ''
    }, // 全局分享信息
    share_success_count: 0, // 分享成功次数
    storeChange: 0, // 作为state的状态改变    
    videoPage: utils.cache('videoPage') ? JSON.parse(utils.cache('videoPage')) : {}
  },
  mutations: {
    setVideoPage (state, val) {
      var obj = Object.assign(state.videoPage)
      obj[val.name] = val.page
      if (state.create_time) {
        if (new Date().getTime() > obj.create_time * 1 + 2 * 3600000) {
          obj = {}
        }
      } else {
        obj.create_time = new Date().getTime()
      }
      state.videoPage = obj
      utils.cache('videoPage', JSON.stringify(obj))
    },
    // 显示隐藏分享成功弹框
    setShowShare (state, bool) {
      state.showSuccessShare = bool
    },
    // 设置分享成功次数
    set_success_count (state, num) {
      state.share_success_count++
    },
    // 设置分享对象
    set_shareSuccessObj (state, {type, shareSuccessObj}) { // 设置分享数据
      state['shareSuccessObj' + type] = shareSuccessObj
    },
    set_shareType (state, shareType) { // 设置分享类型
      state.shareType = shareType
    },
    set_storeChange (state) {
      state.storeChange++
    },
    set_shareCount (state, reset) {
      state.shareCount++
      if (reset) {
        state.shareCount = 1
      }
    }
  }
}



