<template>
  <div id="prompt-box" v-if="dropBol" style="z-index: 1000000">
    <!-- <mt-popup
      v-model="dropBol" @click.stop=''>
      <div class="dropBox">
        <div class="text-center f30">{{contentTxt}}</div>
        <div class="clear btn">
          <div class="fl text-center f30 cancel" @click="leftBtn">{{leftTxt}}</div>
          <div class="fl text-center f30" @click="rightBtn" v-if="rightTxt">{{rightTxt}}</div>
        </div>
      </div>
    </mt-popup> -->
    <div class="maskBox" style="z-index: 10">
      <div class="dropBox">
        <div class="text-center f30">{{contentTxt}}</div>
        <div class="clear btn">
          <div class="fl text-center f30 cancel" @click="leftBtn">{{leftTxt}}</div>
          <div class="fl text-center f30" @click="rightBtn" v-if="rightTxt">{{rightTxt}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'prompt-box',
  props: {
    // 左按钮文本
    leftTxt: {
      type: String,
      default: '取消'
    },
    // 右按钮文本
    rightTxt: {
      type: String,
      default: ''
    },
    // 上面内容
    contentTxt: {
      type: String,
      default: null
    },
    // 是否弹出弹框
    dropBol: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  },
  methods: {
    // 左边按钮
    leftBtn () {
      this.$emit('leftBtn')
    },
    // 右边按钮
    rightBtn () {
      this.$emit('rightBtn')
    }
  }
}
</script>

<style lang="scss" type="text/css">
#prompt-box{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100vh;
  .mint-popup{
    border-radius: 8px;
  }
  .maskBox {
    position: fixed;
    height: 100vh;
    left: 0;
    right: 0;
    top: 0!important;
    zoom: 1;
    bottom: 0;
    background: rgba($color: #000000, $alpha: .5)
  }
  .dropBox{
    width: 600px;
    box-sizing: border-box;
    padding: 50px 50px 0;
    line-height: 40px;
  }
  .btn{
    display: flex;
    border-top: 1px solid #f7f7f7;
    margin-top: 50px;
    margin-left: -50px;
    margin-right: -50px;
    div{
      flex: 1;
      line-height: 90px;
      color: #F5A623;
    }
    .cancel {
      color: #999;
    }
    div:nth-of-type(2){
      border-left: 1px solid #f7f7f7;
    }
  }
}
</style>



