<script>
import {mapState, mapMutations} from 'vuex'
import Clipboard from 'clipboard'
import PromptBox from '../common/prompt-box'
export default {
  components: {
    PromptBox
  },
  computed: mapState({
    share: state => state.share,
    commonState: state => state.common
  }),
  data () {
    // 复制分享地址
    this.copyLink = location.origin + '/?path=' + this.$route.path + '&inviteCode=' + this.$utils.cache('inviteCode')
    return {
      shareTime: '',
      showDownload: false,        // 是否显示下载框
      rewardText: {
        1: '阅读奖励',
        2: '阅读赚车赚房',
        3: '微信认证成功',
        4: '设置邀请码',
        5: '绑定手机成功',
        6: '阅读文章60分钟',
        7: '关注服务号奖励',
        8: '签到成功',
        9: '问券作答',
        10: '开宝箱分享',
        11: '邀请好友分享',
        12: '晒收入奖励',
        13: '实物分享',
        14: '分享文章奖励',
        15: '阅读推送文章',
        16: '观看奖励',
        17: '观看奖励' // 小视频
      },
      gifts: {}, // 大礼包数据
      shareCarInfo: {}, // 分享豪车样式
      userInfo: {},
      bigGiftType: 0
    }
  },
  watch: {
    'share.shareType' (shareType) {
      if (shareType === 9) {
        this.getGifts()
      }
      if (shareType === 8) {
        this.getNewVersion()
      }
      if (shareType === 7) {
        setTimeout(() => {
          this.setShowShare(false)
        }, 1000)
      } else if (this.shareType === 14) {
        if (!this.$utils.isWx() || this.$config.source_style >= 6) {
          this.resetCount()
        }
      }
      this.getCarInfo()
    },
    'share.showSuccessShare' (flag) {
      if (flag) {
        this.getCarInfo()
      }
    },
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        if (this.share.shareType === 9) {
          this.receiveGift()
        }
      }
    }

  },
  filters: {
    setNumber (value) {
      return value > 1000 ? (value / 10000).toFixed(2) + '万' : value
    }
  },
  methods: {
    ...mapMutations([
      'setShowShare', // 设置分享
      'set_shareSuccessObj', // 设置分享数据
      'set_shareType', // 设置分享类型
      'set_success_count', // 设置分享次数
      'set_storageChange', // 改变分享次数
      'common_set_userInfo', // 改变分享信息
      'common_login_box', // 显示登录框
      'set_shareCount', // 设置分享框
      'common_download_count'
    ]),
    // 重置分享次数
    resetCount () {
      this.set_shareCount(true)
    },
    cancelDownload () {
      this.setShowShare(false)
      this.showDownload = false
    },
    // 跳转至下拉框
    toDownload () {
      this.setShowShare(false)
      location.href = this.$config.downloadUrl
    },
    confirm17 () {
      window.open(this.$config.downloadUrl, '_self')
    },
    cancel17 () {
      this.closePage()
    },
    // 跳转到推送地址
    toPushUrl () {
      // 跳转后关闭弹窗
      this.closePage()
      if (this.share.shareSuccessObj13.url) {
        let url = this.share.shareSuccessObj13.url
        if (url.indexOf('http') > -1) {
          window.plus.runtime.openURL(url)
        } else if (url.indexOf('#') > -1) {
          this.$router.push(url.substring(1, url.length))
        } else {
          this.$router.push(url)
        }
      }
    },
    // 获取车的信息
    getCarInfo () {
      let shareCarInfo = null
      if (this.$utils.cache('shareCarInfo')) {
        shareCarInfo = JSON.parse(this.$utils.cache('shareCarInfo'))
        this.shareCarInfo = Object.assign(shareCarInfo, this.share.shareSuccessObj14)
      }
      if (!shareCarInfo) {
        this.$api.get('SHARE_GUILDBROWERS', {}, res => {
          this.shareCarInfo = Object.assign(res.data, this.share.shareSuccessObj14)
          this.$utils.cache('shareCarInfo', JSON.stringify(this.shareCarInfo))
        })
      }
      this.getInviteInfo()
    },
    // 获取邀请信息
    getInviteInfo () {
      if (this.$route.query.inviteCode) {
        if (this.userInfo.nickname) {
          return
        }
        this.$api.get('USER_INVITEINFO', {invitation_code: this.$route.query.inviteCode}, res => {
          if (res.data) {
            this.userInfo = res.data
          }
        })
      }
    },
    // 分享成功返回
    shareBack1 () {
      let shareArc = this.$utils.cache('share') ? JSON.parse(this.$utils.cache('shareArc')) : null
      if (shareArc && shareArc.timestamp * 1 >= this.$utils.getTimeStamp()) {
        shareArc = null
        this.$utils.cache('shareArc', null)
      }
      let shareSuccessObj = {}
      if (!shareArc) {
        shareSuccessObj = {
          makeRate: 1,
          hasGet: 1354,
          baseRate: 0.5
        }
        shareSuccessObj.timestamp = this.$utils.getTodayStamp()
      } else {
        shareSuccessObj = {
          makeRate: shareArc.makeRate * shareArc.count * 0.5 + 0.5,
          hasGet: 1256,
          baseRate: 0.5
        }
        shareSuccessObj.timestamp = this.$utils.getTodayStamp()
      }
      shareSuccessObj.count = shareSuccessObj.count ? shareSuccessObj.count * 1 + 1 : 0
      this.$utils.cache('shareArc', JSON.stringify(shareSuccessObj))
      this.set_shareSuccessObj({
        type: 2,
        shareSuccessObj
      })
      this.set_shareType(2)
    },
    // 点击第二个弹框加速赚钱
    addRate () {
      let shareSuccessObj = {}
      let shareArc = this.$utils.cache('shareArc') ? JSON.parse(this.$utils.cache('shareArc')) : null
      if (shareArc && shareArc.timestamp * 1 >= this.$utils.getTimeStamp()) {
        shareArc = null
        this.$utils.cache('shareArc', null)
      }
      if (!shareArc) {
        shareSuccessObj = {
          makeRate: 0.5,
          hasGet: 1256
        }
      } else {
        shareSuccessObj = {
          makeRate: shareArc.count * 0.5 + 0.5,
          hasGet: 1256
        }
      }
      this.set_shareSuccessObj({
        type: 2,
        shareSuccessObj
      })
      // 显示引导弹框
      this.set_shareType(2)
      this.setShowShare(true)
    },
    // way weixin 显示公众号 download 跳转至下载页
    operateWay (way) {
      if (way === 'download') {
        window.open(this.$config.downloadUrl, '_self')
      } else if (way === 'weixin') {
        this.set_shareType(19)
        this.setShowShare(true)
      }
    },
    // 跳转到页面 以前版本微信分享暂时弃用换方法
    toPage (url) {
      console.log(this.share.shareCount)
      if (!this.$utils.isWx()) {
        this.set_shareCount()
        if (this.share.shareCount === 2) {
          this.$toast({
            message: '分享未成功，请重新分享后才能领车',
            duration: 1500
          })
        } else if (this.share.shareCount >= 2) {
          this.set_shareCount(true)
          window.open(this.$config.downloadUrl, '_self')
        }
      } else {
        if (this.share.shareCount === 1) {
          this.$toast({
            message: '分享未成功，请重新分享后才能领车',
            duration: 1500
          })
        } else if (this.share.shareCount >= 2) {
          if (this.$utils.isWx()) {
            this.set_shareType(19)
            this.setShowShare(true)
          } else {
            this.set_shareCount(true)
            window.open(this.$config.downloadUrl, '_self')
          }
        }
      }
    },
    /**
     * @param {String} type update升级操作， getPrize 获得奖励操作
     */
    toMyPrise (type, jump) {
      let data = {}
      if (type === 'grade') {
        data.type = 'user_upgrade'
      } else {
        // 重置奖品页面的滚动条为0
        this.$utils.cache('awardPag', 1)
        data.type = 'goods_upgrade'
        // 如果不是关闭按钮直接跳转到对应的详情
        if (jump !== 'close') {
          this.$router.push('/prize')
        }
      }
      // 只需通知后台即可
      this.$api.get('RESET_UPDATE', data)
      this.closePage()
    },
    // 领取礼包
    receiveGift () {
      if (!this.commonState.login_bol) {
        this.common_login_box(true)
        return
      }
      if (this.$config.source_style >= 6 || this.$utils.isWx()) {
        this.$api.get('GIFT_BAG', {}, res => {
          let shareSuccessObj = res.data
          this.set_shareType(10)
          this.set_shareSuccessObj({
            type: 10,
            shareSuccessObj
          })
          if (this.$config.source_style >= 6) {
            this.bigGiftType = 1
          } else {
            this.bigGiftType = 0
          }
          let userInfo = this.commonState.userInfo
          userInfo.achieved_gift_bag = 1
          this.common_set_userInfo(userInfo)
        }, res => {
          if (res.code === 1 && res.msg === '您已领过了，请前往钱包查看!') {
            let userInfo = this.commonState.userInfo
            userInfo.achieved_gift_bag = 1
            this.common_set_userInfo(userInfo)
            this.setShowShare(false)
          }
        })
      } else {
        if (this.commonState.userInfo.achieved_gift_bag === 0) {
          this.showDownload = true
        } else if (this.commonState.userInfo.achieved_gift_bag === 1) {
          this.$toast({
            message: '您已经领取过了，请前往钱包查看',
            duration: 1500
          })
          this.setShowShare(false)
        } else {
          console.log('未登录状态')
        }
      }
    },
    // 获取大礼包随机数据
    getGifts () {
      if (!this.gifts.vcoin) {
        this.$api.get('GET_GIFTS', {}, res => {
          this.gifts = res.data
        })
      }
    },
    // 点击立即领取按钮
    getBigBag () {
      if (this.$config.source_style >= 6) {
        this.$toast({
          message: '领取成功，请前往“我的钱包”查看',
          duration: 1500
        })
      } else if (this.$utils.isWx()) {
        // 处理浏览器下载app
        this.$toast({
          message: '领取成功，请前往“我的钱包”查看',
          duration: 1500
        })
        this.$router.push('/my/profit')
      } else {
        this.$utils.openDownUrl()
      }
      this.setShowShare(false)
    },
    // 获取更新版本的数据
    getNewVersion () {
      // this.$api.get('GET_UPDATE', {vsn: '1.0.3'}, res => {
      this.$api.get('GET_UPDATE', {vsn: this.$config.version}, res => {
        res.data.app_store = 1
        this.versionData = res.data
        let shareSuccessObj = res.data
        shareSuccessObj.version = res.vsn
        this.set_shareSuccessObj({
          type: 8,
          shareSuccessObj
        })
      })
    },
    // 关闭更新弹框
    closeUpdate () {
      this.$utils.cache('isShowUpdate', 1)
      if (this.$config.source_style >= 6) {
        if (this.$config.source_style === 6) {
          this.$utils.plusReady(plus => {
            plus.runtime.openURL(this.$config.downloadUrl)
          })
        } else if (this.$config.source_style === 7) {
          if (this.share.shareSuccessObj8.appstore === 1) {
            this.$utils.plusReady(plus => {
              plus.runtime.openURL(this.$config.downloadUrl)
            })
          } else {
            this.common_download_count()
            this.setShowShare(false)
          }
        }
      } else {
        this.setShowShare(false)
      }
    },
    // 分享完成继续分享
    shareContinue () {
      let shareSuccessObj = {}
      let shareArc = this.$utils.cache('shareArc') ? JSON.parse(this.$utils.cache('shareArc')) : null
      if (shareArc && shareArc.timestamp * 1 <= this.$utils.getTimeStamp()) {
        shareArc = null
        this.$utils.cache('shareArc', null)
      }
      if (!shareArc) {
        shareSuccessObj = {
          makeRate: 0.5,
          hasGet: 1256
        }
      } else {
        shareSuccessObj = {
          makeRate: shareArc.count * 0.5 + 0.5,
          hasGet: 1256
        }
      }
      this.set_shareSuccessObj({
        type: 2,
        shareSuccessObj
      })
      // 显示引导弹框
      this.set_shareType(2)
      this.setShowShare(true)
    },
    // 弹框5分享插队
    shareConfirm () {
      if (Clipboard.isSupported()) {
        let copyEl = new Clipboard(this.$refs.Clipboard1)
        copyEl.on('success',  (e) => {
          this.$toast({
            message: '已复制文章链接,快发送给好友吧',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          // 分享成功对人数和时间随机减少分享次数加1
          this.share.shareSuccessObj5.remainNum = this.$utils.ranDomNum(this.share.shareSuccessObj5.remainNum, 'reduce', 3)
          this.share.shareSuccessObj5.remainTime = this.$utils.ranDomNum(this.share.shareSuccessObj5.remainTime, 'reduce', 20)
          this.totalTime = this.share.shareSuccessObj5.remainTime
          this.set_shareSuccessObj({
            type: 5,
            shareSuccessObj: this.share.shareSuccessObj5
          })
          this.set_success_count(this.share.share_success_count + 1)
          this.setShowShare(false)
          copyEl.destroy()
        })
        copyEl.on('error',  (e) => {
          this.$toast({
            message: '复制链接失败了',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          copyEl.destroy()
        })
      }
    },
    shareBack () {
      this.setShowShare(false)
      this.$router.push('/share/' + this.$utils.ranDomNum(4, 'reduce'))
    },
    // 关闭分享
    closeShare () {
      if (this.share.shareType === 2) {
        this.setShowShare(false)
      }
      // this.setShowShare(false)
    },
    closePage () {
      // 显示过不再做处理缓存
      if (this.share.shareType === 8) {
        this.$utils.cache('isShowUpdate', 1)
      }
      this.setShowShare(false)
    },
    // 取消冒泡
    stopBubble () {
      
    },
    // 关闭定时器
    clearInterVal () {
      clearInterval(this.timer)
      this.timer = null
      this.shareTime = '00分钟00秒'
      return 0
    },
    // 初始化定时器
    initData () {
      if (this.share.shareType === 5) {
        let count = 0
        this.totalTime = this.share.shareSuccessObj5.remainTime
        this.shareTime = this.$utils.transformStoM(this.totalTime)
        this.timer = setInterval(() => {
          if (this.totalTime <= 0) {
            return this.clearInterVal()
          }
          this.shareTime = this.$utils.transformStoM(this.totalTime)
          --this.totalTime
          count++
          if (count >= 40) {
            this.share.shareSuccessObj5.remainNum = this.$utils.ranDomNum(this.share.shareSuccessObj5.remainNum, 'reduce', 5)
            count = 0
          }
          this.share.shareSuccessObj5.remainTime = this.totalTime
          this.set_shareSuccessObj({
            type: 5,
            shareSuccessObj: this.share.shareSuccessObj5
          })
        }, 1000)
      } else if (this.share.shareType === 9) {
        this.getGifts()
      }
    }
  },
  created () {
    this.initData()
  },
  mounted () {
    if (this.share.shareType === 7) {
      var time = 1500
      if (this.share.shareSuccessObj7.type === 16) {
        time = 800
      } else {
        time = 1500
      }
      setTimeout(() => {
        this.setShowShare(false)
      }, time)
    }
    this.getCarInfo()
    // 显示类型为8的时候显示获取版本更新数据
    if (this.share.shareType === 8) {
      this.getNewVersion()
    } else if (this.share.shareType === 14) {
      if (!this.$utils.isWx() && this.$config.source_style >= 6) {
        this.resetCount()
      }
    }
  },
  beforeDestroy () {
    if (this.share.shareType === 5) {
      this.clearInterVal()
    }
  }
}
</script>

