// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import MintUI from 'mint-ui'
import 'swiper/dist/css/swiper.min.css'
import 'mint-ui/lib/style.css'
import './assets/css/common.css'
import './assets/css/transition.css'                 // 自定义动画css
import 'vue-photo-preview/dist/skin.css'
import './assets/css/second.scss'
import axios from 'axios'
import store from './store'
import beforeEach from './router/beforeEach'         // 路由钩子跳转前函数
import afterEach from './router/afterEach'           // 路由钩子跳转后函数
import * as $utils from './assets/js/utils'          // 公共自定义钩子函数
import * as $api from './assets/js/api'              // api请求函数
import * as $reg from './assets/js/regular'          // 正则             
import VueTouch from 'vue-touch'
import config from './assets/js/config'
import qs from 'qs'
import vuePicturePreview from 'vue-photo-preview'
import '../src/assets/js/touch'
require('es6-promise').polyfill()
require('es6-promise/auto')
Vue.use(vuePicturePreview)
Vue.use(VueTouch, {name: 'v-touch'})                // 挂载全局的左右拖动插件
VueTouch.config.swipe = {                       // 释放上下滑动
  directions: ['up', 'down', 'left', 'right', 'horizontal', 'vertical', 'all'],
  threshold: 100                                    // 手指左右滑动距离
}

Vue.prototype.$api = $api
Vue.prototype.$qs = qs
Vue.prototype.$utils = $utils
Vue.prototype.axios = axios
Vue.prototype.$config = config
Vue.prototype.$reg = $reg
Vue.prototype.back = () => {
  router.go(-1)
}
Vue.config.productionTip = false
Vue.prototype.back = function () {
  this.$router.go(-1)
}
Vue.use(MintUI, {
  lazyload: {
    preLoad: 0,
    error: 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/default_image.png',
    loading: 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/gray_loading.png',
    attempt: 1,
    filter: {
      progressive (listener) {
        const isCDN = /qiniudn.com/
        if (isCDN.test(listener.src)) {
          listener.el.setAttribute('lazy-progressive', 'true')
          listener.loading = listener.src + '?imageView2/1/w/10/h/10'
        }
      },
      webp (listener) {
        listener.src = $utils.exceptWebp(listener.src, 1)
      }
    }
  }
})
$utils.isSupportLocalStorage()
$utils.cache('phoneModel', JSON.stringify($utils.mobileDetect()))  // 手机型号和操作系统
/* eslint-disable no-new */
store.commit('common_login_bol', typeof $utils.cache('user_token') === 'string')
if ($utils.isWx()) {
  if (!store.state.common.login_bol) {
    $utils.wxLogin()  // 微信登录
  }
}
router.beforeEach((to, from, next) => { // 跳转路由前钩子函数
  beforeEach(to, from, next)
})
router.afterEach((to, from, next) => {  // 跳转路由后钩子函数
  afterEach(to, from, next)
})
Vue.filter('setNumber', function (value) {
  if (value > 10000) {
    return (Math.round(value / 10000)) + 'w'
  } else {
    return value
  }
})
Vue.filter('setPeople', function (value) {
  if (value > 10000) {
    return (Math.round(value / 10000)) + '万'
  } else {
    return value
  }
})
Vue.filter('setFloatNumber', function (value) {
  if (value > 10000) {
    return (value / 10000).toFixed(2) + 'w'
  } else {
    return value
  }
})
Vue.filter('setMinute', function (value) {
  let minute = parseInt(value / 60)
  let second = value % 60
  minute = minute >= 10 ? minute : '0' + minute
  second = second >= 10 ? second : '0' + second
  return `${minute}:${second}`
})
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



