import Vue from 'vue'
import Router from 'vue-router'
import secondRouter from './second'
import movie from './movie'
// 首页
// const HomeIndex = r => require.ensure(
//   [],
//   () => r(require('@/components/home/index')),
//   'chunkname1'
// )
// 文章详情
const ArticleDetails = r => require.ensure(
  [],
  () => r(require('@/components/home/article-details')),
  'ArticleDetails'
)
// 本地文章详情
const LocalDetails = r => require.ensure(
  [],
  () => r(require('@/components/indexList/local-details')),
  'LocalDetails'
)
// 本地文章详情404
const notFound = r => require.ensure(
  [],
  () => r(require('@/components/common/not-found')),
  'notFound'
)
// 个人中心
const PersonalCenter = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/index')),
  'chunkname2'
)
// 个人中心-关注
const FocusCenter = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/focus-center')),
  'chunkname3'
)
// 个人中心-关注-媒体号
const MediaNumber = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/media-number')),
  'chunkname3'
)
// 个人中心-收藏
const Collection = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/collection')),
  'chunkname4'
)
// 个人中心-历史
const Historys = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/history')),
  'chunkname5'
)
// 个人中心-用户反馈
const AwardFeedback = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/award-feedback')),
  'chunkname6'
)
// 个人中心-关于我们
const AboutUs = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/about-us')),
  'chunkname7'
)

// 城市选择
const SelectCity = r => require.ensure(
  [],
  () => r(require('@/components/common/select-city')),
  'chunkname1'
)

// dome
const Dome = r => require.ensure(
  [],
  () => r(require('@/components/common/dome')),
  'Dome'
)
// 赚车赚房
const richIndex = r => require.ensure(
  [],
  () => r(require('@/components/richPlay/richIndex')),
  'richIndex'
)

// 富豪榜
const RichList = r => require.ensure(
  [],
  () => r(require('@/components/richPlay/richList')),
  'RichList'
)

// 暴富
const BurstRich = r => require.ensure(
  [],
  () => r(require('@/components/burst_rich/index')),
  'BurstRich'
)
// 奖励
const AwardPag = r => require.ensure(
  [],
  () => r(require('@/components/awardPag/index')),
  'AwardPag'
)
// 汽车详情
const CarDetail = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/car_detail/index')),
  'CarDetail'
)
// 商品详情
const GoodsDetail = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/goods_detail/index')),
  'GoodsDetail'
)
// 房产详情
const HouseDetail = r => require.ensure(
  [],
  () => r(require('@/components/personal-center/house_detail/index')),
  'HouseDetail'
)

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/Dome',
    name: 'Dome',
    component: Dome
  },
  // 首页
  // {
  //   path: '/',
  //   name: 'HomeIndex',
  //   component: HomeIndex,
  //   meta: {
  //     reload_address: '',      // 登录后跳转地址
  //     keepAlive: true,         // 是否缓存页面
  //     title: '掘金宝-看资讯视频，赚车子赚房子',
  //     bgcolor: '#ffaa00'
  //     // title: '《掘金宝》想发财，上掘金链！'
  //   }
  // },
  // 城市选择
  {
    path: '/SelectCity',
    name: 'SelectCity',
    component: SelectCity,
    meta: {
      reload_address: '',      // 登录后跳转地址
      keepAlive: false,         // 是否缓存页面
      title: '城市选择'
    }
  },
  // 文章详情
  {
    path: '/ArticleDetails/:aid',
    name: 'ArticleDetails',
    component: ArticleDetails,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '文章详情'
    }
  },
  // 文章详情404
  {
    path: '/notFound',
    name: 'notFound',
    component: notFound,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '页面丢失'
    }
  },
  // 本地文章详情
  {
    path: '/LocalDetails/:aid',
    name: 'LocalDetails',
    component: LocalDetails,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '文章详情'
    }
  },
  // 个人中心
  {
    path: '/personal_center',
    name: 'PersonalCenter',
    component: PersonalCenter,
    meta: {
      reload_address: '',
      keepAlive: true,
      title: '掘金宝-看资讯视频，赚车子赚房子',
      bgcolor: '#ff9b00'
    }
  },
  // 个人中心-关注
  {
    path: '/FocusCenter',
    name: 'FocusCenter',
    component: FocusCenter,
    meta: {
      reload_address: '/personal_center',
      keepAlive: false,
      title: '我的关注',
      bgcolor: '#ffaa00'
    }
  },
  // 个人中心-关注-媒体号
  {
    path: '/MediaNumber/:mid',
    name: 'MediaNumber',
    component: MediaNumber,
    meta: {
      reload_address: '/personal_center',
      keepAlive: false,
      title: '媒体号',
      bgcolor: '#ffaa00'
    }
  },
  // 个人中心-收藏
  {
    path: '/Collection',
    name: 'Collection',
    component: Collection,
    meta: {
      reload_address: '/personal_center',
      keepAlive: false,
      title: '我的收藏',
      bgcolor: '#ffaa00'
    }
  },
  // 个人中心-历史
  {
    path: '/Historys',
    name: 'Historys',
    component: Historys,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '历史阅读',
      bgcolor: '#ffaa00'
    }
  },
  // 个人中心-用户反馈
  {
    path: '/AwardFeedback',
    name: 'AwardFeedback',
    component: AwardFeedback,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '用户反馈',
      bgcolor: '#ffaa00'
    }
  },
  // 个人中心-关于我们
  {
    path: '/AboutUs',
    name: 'AboutUs',
    component: AboutUs,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '关于我们',
      bgcolor: '#ffaa00'
    }
  },
  // 富豪榜
  {
    path: '/rich_list',
    name: 'RichList',
    component: RichList,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '富豪榜'
    }
  },
  // 赚车赚房
  {
    path: '/richIndex',
    name: 'richIndex',
    component: richIndex,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '赚车赚房'
    }
  },
  // 赚钱指南
  {
    path: '/burst_rich',
    name: 'BurstRich',
    component: BurstRich,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '赚钱指南'
    }
  },
    // 奖励页
  {
    path: '/prize',
    name: 'AwardPag',
    component: AwardPag,
    meta: {
      reload_address: '',
      keepAlive: true,
      title: '查看奖品'
    }
  },
    // 汽车详情
  {
    path: '/car_detail/:id',
    name: 'CarDetail',
    component: CarDetail,
    meta: {
      reload_address: '',
      keepAlive: true,
      title: '汽车详情'
    }
  },
    // 商品详情
  {
    path: '/goods_detail/:id',
    name: 'GoodsDetail',
    component: GoodsDetail,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '商品详情'
    }
  },
  // 房产详情
  {
    path: '/house_detail/:id',
    name: 'HouseDetail',
    component: HouseDetail,
    meta: {
      reload_address: '',
      keepAlive: false,
      title: '房产详情'
    }
  },
  ...secondRouter,
  ...movie
  ]
})



