/**
 *
 * @param {string} str 需要去首尾空格的字符串
 * @returns {string} 返回一进去出号的字符串
 */
export const trim = (str) => {
  return str.replace(/^\s\s*/, '').replace(/\s\s*$/, '')
}

export const getParams = (str) => {
  let obj = {}
  let arr = str.split('&')
  for (let v of arr) {
    let temp = v.split('=')
    obj[temp[0]] = temp[1]
  }
  return obj
}
