/**
 * @param { Object } el 将vue实例this传入
 */
export const setLogin = (el) => {
  let codeLength = strTimes(location.href, 'code')
  if (codeLength === 1 && location.href.toLocaleLowerCase().indexOf('invitecode') === -1) {
    let stateParams = getParams(location.search.substr(1, location.search.length - 1))
    let str = JSON.parse(Base64.decode(stateParams.state))
    let data = {
      code: stateParams.code,
      type: 1
    }
    data = Object.assign(data, registerInfo())
    // 添加邀请码修改
    if (str.inviteCode) {
      data.invitation = str.inviteCode
    // 兼容邀请码 
    } else if (str.invitecode) {
      data.invitation = str.invitecode
    }
    data.origin_url = location.origin
    $api.post('WECHAT_LOGIN', data, (res) => {
      cache('uid', res.data.uid)
      cache('user_token', res.data.user_token)
      cache('nickname', res.data.nickname)
      cache('first_login', res.data.first_login)
      cache('user_icon', res.data.avatar)
      cache('inviteCode', res.data.invitation)
      cache('userInfo', JSON.stringify(res.data))
      if (res.data.unionid) {
        cache('unionid', res.data.unionid)
      }
      cache('handOperated', null) // 登录成功去除手动退出标志
      cache('showLoginSuccess', true)
      el.$store.commit('common_login_bol', true)
      el.$store.commit('common_set_userInfo', res.data)
      $api.get('MY_CHANNEL', {}, (res) => {
        el.$store.common_get_channel(res.data, 'my_channel')
      })
      $api.get('RECOND_CHANNEL', {}, (res) => {
        el.$store.common_get_recondChannel(res.data, 'recond_channel')
      })
      if (str.path) {
        let url = location.origin + '/#' + decodeURIComponent(str.path)
        // 邀请码
        if (str.inviteCode) {
          url = url + '?inviteCode=' + str.inviteCode
        }
        // 分享回调
        if (str.call) {
          if (url.indexOf('?') > -1) {
            url = url + `&call=${str.call}`
          } else {
            url = url + `?call=${str.call}`
          }
        }
        // 汽车id
        if (str.gid) {
          if (url.indexOf('?') > -1) {
            url = url + `&gid=${str.gid}`
          } else {
            url = url + `?gid=${str.gid}`
          }
        }
        location.href = url
      } else {
        location.href = location.origin
      }
    })
  }
}
/**
 * @returns {number} 返回一个滚动条距离底部的距离
 */
export const scrollHeight = () => {
  // 窗口可视范围的高度
  let height = getClientHeight()
  // 窗口滚动条高度
  let theight = getScrollTop()
  // 窗口可视范围的高度
  let rheight = getScrollHeight()
  // 滚动条距离底部的高度
  let bheight = rheight - theight - height
  return bheight
}

/**
 *
 * @param {object} 把上传的文件e放进来判断是不是图片
 */
export const isImg = (e) => {
  if (typeof FileReader !== 'undefined') {
    let file = e.target.files[0]
    if ((file.type).indexOf('image/') === -1) {
      return false
    }
  } else {
    let fileName = e.target.value
    let suffixIndex = fileName.lastIndexOf('.')
    let suffix = fileName.substring(suffixIndex + 1).toUpperCase()
    if (suffix !== 'BMP' && suffix !== 'JPG' && suffix !== 'JPEG' && suffix !== 'PNG' && suffix !== 'GIF') {
      return false
    }
  }
  return true
}

/**
 *
 * @param {number} num 传入数字大于1W的化整为w
 */
export const thousandRead = (num, length, text = 'w') => {
  num = num || 0
  let long = length || 2
  if (num > 9999) {
    return (num / 10000).toFixed(long) + text
  } else {
    return parseInt((num * 1))
  }
}

/**
 * @param {number} type 1为新增关注2为取消关注
 * @param {number} mid 媒体号id
 */
// add_attention       需要增加关注的数组本地储存key值
// cancel_attention    需要取消关注的数组本地储存key值
export const refreshAttention = (type, mid) => {
  let addArr = cache('add_attention') ? JSON.parse(cache('add_attention')) : [] // 增加的数组
  let cancelArr = cache('cancel_attention') ? JSON.parse(cache('cancel_attention')) : [] // 删除的数组
  switch (type) {
    case 1:
      addArr.push(mid)
      cache('add_attention', JSON.stringify(addArr))
      for (let i in cancelArr) {
        if (cancelArr[i] === mid) {
          cancelArr.splice(i, 1)
          cache('cancel_attention', JSON.stringify(cancelArr))
          break
        }
      }
      break
    case 2:
      cancelArr.push(mid)
      cache('cancel_attention', JSON.stringify(cancelArr))
      for (let i in addArr) {
        if (addArr[i] === mid) {
          addArr.splice(i, 1)
          cache('add_attention', JSON.stringify(addArr))
          break
        }
      }
      break
    default:
      break
  }
}
export const uploadAttach = (data) => {
  let contentLen = Math.round(data.file.size * 100 / 1024) / 100
  let fd = new FormData()
  fd.append('Content-Length', contentLen)
  fd.append('OSSAccessKeyId', data.OSSAccessKeyId)
  fd.append('policy', data.policy)
  fd.append('signature', data.signature)
  // 文件名字
  fd.append('key', data.path + data.name)
  fd.append('dir', data.path)
  fd.append('success_action_status', '200')
  // 文件必须声明在最后，否则异常
  fd.append('file', data.file)
  return new Promise((resolve, reject) => {
    axios.post(data.imgServer, fd, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      transformRequest: (data) => {
        return data
      }
    }).then(res => {
      resolve(res)
    }).catch(err => {
      reject(err)
    })
  })
}
/**
 * 上传文件
 * @param {String} usertoken 用户登录token
 * @param {String} dir 文件夹
 * @param {Object} file 文件对象
 * @param {String} fileName 文件名 // 不传为当前时间毫秒数，传了为自己的文件名称不需要
 */
export const uploader = (usertoken, dir, file, fileName = new Date().getTime()) => {
  return new Promise((resolve, reject) => {
    $api.get('OSS', {
      user_token: usertoken,
      dir
    }, (res) => {
      let data = {}
      data.imgServer = res.data.host
      data.OSSAccessKeyId = res.data.accessid
      data.policy = res.data.policy
      data.signature = res.data.signature
      data.path = res.data.dir
      let format = file.name.substring(file.name.lastIndexOf('.', file.name.length - 1))
      fileName = fileName + format
      data.name = fileName
      data.file = file
      uploadAttach(data).then(res => {
        resolve({
          res: res,
          url: data.imgServer + '/' + data.path + data.name
        })
      })
    })
  })
}

// 获取窗口可视范围的高度
export const getClientHeight = () => {
  let clientHeight = 0
  if (document.body.clientHeight && document.documentElement.clientHeight) {
    clientHeight = (document.body.clientHeight < document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
  } else {
    clientHeight = (document.body.clientHeight > document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
  }
  return clientHeight
}
// 获取设备宽度
export const getDevideWidth = () => {
  return document.documentElement.clientWidth || document.body.clientWidth
}

/**
 * 获取窗口或元素滚动条高度
 * @param {Object} el dom元素 
 */
export const getScrollTop = (el) => {
  let scrollTop = 0
  if (el) {
    scrollTop = el.scrollTop
  } else {
    if (document.documentElement && document.documentElement.scrollTop) {
      scrollTop = document.documentElement.scrollTop
    } else if (document.body) {
      scrollTop = document.body.scrollTop
    }
  }
  return scrollTop
}

// 获取文档内容实际高度
export const getScrollHeight = () => {
  return Math.max(document.body.scrollHeight, document.documentElement.scrollHeight)
}
/**
 *
 * @param {string} str 传递图片地址
 * @returns {string} 返回去除.webp 的一个地址
 */

export const exceptWebp = (str, isDouban) => {
  if (typeof str === 'string') {
    let length = str.length
    if (str.substring(length - 5, length) === '.webp') {
      if (isDouban) {
        return str.replace('.webp', '.jpg')
      } else {
        return str.substring(0, length - 5)
      }
    } else {
      return str
    }
  } else {
    return str
  }
}
