/**
 *
 * @param {string} str 传递字符串进来
 * @param {number} length 需要切割的字符串长度，未传则默认6个
 * @returns {string} 返回切割后的字符串
 */
export const splitStr = (str, length) => {
  if (typeof str === 'string') {
    if (typeof length === 'number') {
      return str.length > length ? str.substr(0, length) + '...' : str
    } else {
      return str.length > 6 ? str.substr(0, 6) + '...' : str
    }
  } else {
    return str
  }
}
