/**
 * 将秒转化成分钟和秒数
 * @param {Number} s 传递秒数进来
 * @returns {String} 返回分秒字符串
 */
export const transformStoM = (s) => {
  if (typeof s === 'number') {
    let m = parseInt(s / 60)
    let seconds = s % 60
    m = m >= 10 ? m : '0' + m
    seconds = seconds >= 10 ? seconds : '0' + seconds
    return `${m}分钟${seconds}秒`
  } else {
    return '0分0秒'
  }
}
/**
 * 随机加或者随机减少
 * @param {Number} current 当前数量
 * @param {Number} base 基数
 * @param {String} type 'add'为加'reduce为减'
 * @returns {Number} 返回随机的人数
 */
export const ranDomNum = (current, type, base) => {
  if (!base) {
    base = current
  }
  base = Math.ceil(Math.random() * base)
  if (type === 'add') {
    return (current + base)
  } else if (type === 'reduce') {
    return (current - base < 0) ? 0 : (current - base)
  } else {
    return current
  }
}
/**
 * 判断是否需要请求该篇文章的接口
 * @param {String} id 文章id
 * @returns { Boolean } 返回是符合请求条件
 */
export const isGetReard = (id) => {
  if (!cache('articleReward')) {
    return true
  } else {
    let articleReward = JSON.parse(cache('articleReward'))
    if (getTimeStamp() < articleReward.timeStamp) {
      if (articleReward[id]) {
        return false
      } else {
        return true
      }
    } else {
      cache('articleReward', null)
      return true
    }
  }
}

/**
 *
 * @param {String} id
 * @param {String} type article, smallVideo, video
 * 设置文章id已阅读
 * @param {String} id
 * @param {String} type article, smallVideo, video
 */
export const setArticle = (id, type) => {
  let obj = {}
  let d = timestampToStr(new Date(), 'Y/M/D') + ' 23:59:59'
  let currentTime = parseInt(new Date(d).getTime() / 1000)
  obj.timeStamp = currentTime
  if (!cache('articleReward')) {
    obj[id] = true
    obj.list = [{id, type}]
  } else {
    obj = JSON.parse(cache('articleReward'))
    obj[id] = true
    obj.list.push({id, type})
  }
  cache('articleReward', JSON.stringify(obj))
}
/**
 * 计算两个时间戳内的时间差
 * @param {number} startTime
 * @param {number} expiredTime
 */
export const timeDifference = (startTime, expiredTime) => {
  if (startTime >= expiredTime) {
    return {
      num: [0, 0, 0, 0],
      string: ['0天', '0时', '0分', '0秒']
    }
  } else {
    let cha = expiredTime - startTime
    let remainingTime = cha % 86400000
    let remainingHours = remainingTime % 3600000
    let days = parseInt(cha / 86400000)
    let hours = parseInt(remainingTime / 3600000)
    let minute = parseInt(remainingHours / 60000)
    let second = parseInt((remainingHours % 60000) / 1000)
    days = days < 10 ? '0' + days : days
    hours = hours < 10 ? '0' + hours : hours
    minute = minute < 10 ? '0' + minute : minute
    second = second < 10 ? '0' + second : second
    return {
      num: [days, hours, minute, second],
      string: [`${days}天`, `${hours}时`, `${minute}分`, `${second}秒`]
    }
  }
}
