// 秒转时间
export const timestampTIme = (time) => {
  let i = 0
  let s = parseInt(time)
  if (s > 60) {
    i = parseInt(s / 60)
    s = parseInt(s % 60)
  }
  var zero = function (v) {
    return v >= 10 ? v : '0' + v
  }
  return  zero(i) + ':' + zero(s)
}

