const isNewSdk = true
export const androidFullScreen = () => {
  if (isNewSdk) {
    var self = window.plus.webview.currentWebview()
    self.setStyle({
      videoFullscreen: 'landscape'
    })
  } else {
    document.addEventListener('webkitfullscreenchange', function () {
      var el = document.webkitFullscreenElement // 获取全屏元素
      if (el) {
        window.plus.screen.lockOrientation('landscape') // 锁死屏幕方向为横屏
      } else {
        window.plus.screen.unlockOrientation() // 解除屏幕方向的锁定
      }
    })
  }
}
// iOS全屏
export const iOSfullScreen = (videoElem) => {
  // 监听的事件与Android平台有很大区别
  videoElem.addEventListener('webkitbeginfullscreen', function () {
    window.plus.screen.lockOrientation('landscape') // 锁死屏幕方向为横屏
    window.plus.navigator.setFullscreen(true)
  })
  videoElem.addEventListener('webkitendfullscreen', function () {
    videoElem && videoElem.setAttribute('controls', 'controls')
    window.plus.screen.lockOrientation('portrait') // 锁死屏幕方向为竖屏  
    window.plus.navigator.setFullscreen(false)
  })
}
// 监听视屏横屏
export const setfullScreen = (el) => {
  plusReady((plus) => {
    let osName = plus.os.name
    if (osName === 'Android') {
      androidFullScreen()
    } else if (osName === 'iOS') {
      iOSfullScreen(el)
    }
  })
}
export const backHisTory = (hash) => {
  if (window.history && window.history.pushState) {
    window.addEventListener('popstate', function () {
      window.history.pushState('forward', null, '#')
      window.history.forward(1)
      window.parent.location.hash = hash  // 这里写你自己要回退的路径
    })
  }
  window.history.pushState('forward', null, '#') // 在IE中必须得有这两行
  window.history.forward(1)
}
/**
 * 删除元素
 * @param {Object} el dom节点
 */
export const emptyEl = el => {
  let doms = el.children
  if (doms && doms.length > 0) {
    for (let v of doms) {
      v.parentNode.removeChild(v)
    }
  } else {
    return null
  }
}

export const setElHeight = (width, els, index) => {
  let img = document.createElement('img')
  if (!els[0]) {
    return
  }
  img.src = els[index].src
  img.onload = function () {
    let height = img.height / (img.width / width)
    let div = document.createElement('div')
    div.className = 'img_div'
    div.style.width = width + 'px'
    div.style.height = Math.round(height) + 'px'
    els[index].parentNode.insertBefore(div, els[index])
    els[index].removeAttribute('style')
    div.style.background = `url(${els[index].src}) no-repeat left top`
    div.style.backgroundSize = `100% 100%`
    div.style.marginBottom = '10px'
    div.style.borderRadius = '10px'
    div.appendChild(els[index])
    img = null
    if ((index + 1) < els.length) {
      setElHeight(width, els, index + 1)
    }
  }
}



