<template>
  <div class="footer" :class="{fourTab: fourTab}">
    <div class="footer-item" v-for="(item,index) in arr"
      :key="item.txt" v-if="typeof item.show === 'undefined' || item.show === 1" :class="{redColor:commonState.hasRed[item.to]?'redColor':'', active: item.txt === '赚车赚房'}"
      @click="toPage(item.to, index)" @dblclick='updateList(index)'>
      <div class="make_money iconSize" v-if="item.txt === '赚车赚房'">
        <img src="../../assets/img/richList/make_money.png" alt="赚车赚房">
      </div>
      <div v-else>
        <div class="imgDiv" @dblclick="updateList(index)">
          <img :src="$route.path === item.to?item.img2:item.img1" alt="">
        </div>
        <p class="f20 lh24" :class="{'colorTxt':$route.path === item.to}">{{item.txt}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'footer-index',
  computed: mapState({
    commonState: state => state.common
  }),
  props: ['imgIndex'],
  data () {
    return {
      arr: [],
      fourTab: false,
      routers: {
        '/': 0,
        '/movie': 1,
        '/make_money': 2,
        '/task': 3,
        '/personal_center': 4
      },
      tempArr: [
        {txt: '首页', to: '/', img1: require('../../assets/img/footerImg3.png'), img2: require('../../assets/img/footerImg1.png')},
        {txt: '影视', to: '/movie', img1: require('../../assets/img/movie/movie.png'), img2: require('../../assets/img/movie/movie_active.png')},
        {txt: '赚车赚房', to: '/make_money', img1: require('../../assets/img/make_money.png'), img2: require('../../assets/img/make_money_active.png')},
        {txt: '任务', to: '/task', img1: require('../../assets/img/task.png'), img2: require('../../assets/img/task_active.png')},
        {txt: '我的', to: '/personal_center', img1: require('../../assets/img/footerImg2.png'), img2: require('../../assets/img/footerImg4.png')}
      ]
    }
  },
  methods: {
    toPage (to, index) {
      this.$router.replace({path: to})
      if (to === '/make_money') {
        let time = new Date(new Date(new Date().toLocaleDateString()).getTime() + 24 * 60 * 60 * 1000 - 1).getTime()
        this.$utils.cache('redTime', time)
      }
    },
    getMenu () {
      this.arr = []
      let d = new Date().getTime()
      if (!this.$utils.cache('menu') || d > JSON.parse(this.$utils.cache('menu')).time) {
        setTimeout(() => {
          // 做底部请求不到的处理
          if (this.arr.length === 0) {
            this.arr = this.tempArr
            this.$utils.cache('menu', JSON.stringify({
              arr: this.tempArr,
              time: new Date().getTime()
            }))
            this.arr = this.tempArr
          }
        }, 2000)
        this.$api.get('SYSTEM_MENU', {vsn: this.$config.version}, res => {
          let arr = []
          let temp = JSON.parse(JSON.stringify(res.data))
          let count = 0
          if (res.data) {
            for (let v of res.data) {
              if (v.show === 1) {
                count++
              }
              for (let v1 of this.tempArr) {
                if (v.txt === v1.txt) {
                  let obj = Object.assign(v, v1)
                  arr.push(obj)
                  break
                }
              }
            }
            this.arr = arr
            if (count === 4) {
              this.fourTab = true
            }
            this.$utils.cache('menu', JSON.stringify({
              time: new Date().getTime() + 3 * 24 * 3600 * 1000,
              arr: temp
            }))
          }
        })
      } else {
        let arr = JSON.parse(this.$utils.cache('menu')).arr
        let tempArr = []
        let count = 0
        for (let v of arr) {
          if (v.show === 1) {
            count++
          }
          for (let v1 of this.tempArr) {
            if (v.txt === v1.txt) {
              let obj = Object.assign(v, v1)
              tempArr.push(obj)
              break
            }
          }
        }
        if (count === 4) {
          this.fourTab = true
        }
        this.arr = tempArr
      }
    },
    // 点击双击事件
    updateList (index) {
      if (index === 0) {
        this.common_set_dblickLoading(1)
        this.common_set_dobule()
      }
    },
    updata () {
      this.$emit('updata')
    },
    ...mapMutations([
      'common_login_box',
      'common_set_guild',
      'common_set_hasRed',
      'common_set_vcoin',
      'common_set_dobule',
      'common_set_dblickLoading'
    ]),
    login () {
      this.common_login_box(true)
    },
    // 判断是否显示连续签到7天领金币浮窗
    setShowGuild () {
      let showGuild = this.$utils.cache('showGuild') ? JSON.parse(this.$utils.cache('showGuild')) : null
      if (showGuild) {
        if (this.$utils.getTimeStamp() > showGuild.time) {
          delete showGuild.isShow
        }
      }
      if (this.commonState.userInfo.is_new * 1 === 0 && ((!showGuild) || ((this.$utils.getTimeStamp() < showGuild.time) && showGuild.count < 3 && showGuild.isShow !== 1))) {
        this.common_set_guild(true)
      }
    },
    // 隐藏领取金币浮窗
    hideGuild () {
      let showGuild = {}
      showGuild.time = this.$utils.getTimeStamp()
      showGuild.isShow = 1
      this.$utils.cache('showGuild', false)
      this.common_set_guild(false)
      this.$router.replace('/')
    },
    getRedActive () {
      this.$api.get('MESSAGE_HINT', {}, res => {
        let data = {
          '/': false,
          '/make_money': res.data.wealth_plan === 1,
          '/task': res.data.unsigned === 1,
          '/personal_center': res.data.msg >= 1
        }
        if (this.$utils.cache('redTime')) {
          if (this.$utils.cache('redTime') < new Date().getTime()) {
            data['/make_money'] = true
          } else {
            data['/make_money'] = false
          }
        } else {
          data['/make_money'] = true
        }
        this.common_set_vcoin(res.data.reward_coefficient)
        this.common_set_hasRed(data)
      })
    }
  },
  watch: {
    $route: function (a) {
      if (this.routers[a.path] === this.imgIndex) {
        this.setShowGuild()
        this.getRedActive()
      }
    },
    'commonState.login_bol' (flag) {
      if (flag) {
        this.getRedActive()
      }
    },
    // 双击加载中
    'commonState.dbclickLoading' (flag) {
      if (flag === 1) {
        this.$set(this.arr, 0, {txt: '加载中', to: '/', img1: require('../../assets/img/footerImg3.png'), img2: require('../../assets/img/refresh.gif')})
      } else {
        this.$set(this.arr, 0, {txt: '首页', to: '/', img1: require('../../assets/img/footerImg3.png'), img2: require('../../assets/img/footerImg1.png')})
      }
    }
  },
  created () {
    this.setShowGuild()
    this.getRedActive()
    if (this.$config.source_style >= 6) {
      this.getMenu()
    } else {
      this.arr = this.tempArr
    }
  },
  mounted () {
    
  }
}
</script>

<style lang="scss" scoped>
.footer{
  position: fixed;
  bottom: 0px;
  height: 100px;
  background: #fff;
  border-top: 1px solid rgba(221,221,221,.6);
  display: flex;
  left: 0;
  right: 0;
  justify-content: space-between;
  .make_money {
    position: relative;
    width: 169px;
    flex: auto;
    height: 129px;
    margin-top: -30px;
    img {
      width: 100%;
      height: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  .footer-item {
    flex: 1;
    min-width: 0;
    position: relative;
    text-align: center;
    width: 20%;
    box-sizing: border-box;
    &.active {
      width: 184px;
      flex: inherit;
      &.redColor::after {
        right: 30px;
      }
    }
  }
  .btn{
    height: 100px;
    position: absolute;
    background: transparent;
  }
  .login{
    width: 100%;
    position: absolute;
    height: 100px;
    line-height: 100px;
    padding: 0 30px;
    box-sizing: border-box;
    top: -100px;
    background: #F4F4F4;
    .fr{
      background: #fa0;
      width: 120px;
      height: 52px;
      color: #fff;
      line-height: 52px;text-align: center;border-radius: 10px;margin-top: 24px;
    }
  }
  .imgDiv{
    width: 44px;
    height: 44px;
    display: block;
    margin: 13px auto 0;
    img{
      width: 100%;
      height: 100%;
    }

  }
  .colorTxt{
    color: #fa0;
  }
  p{
    margin-top: 12px;
    color: #b2b0b0;
  }
  .task-guild{
    position: absolute;
    width: 300px;
    height: 60px;
    border-radius: 15px;
    background: #D42E00;
    color: #fff;
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 268px;
    white-space: nowrap;
    top: -80px;
    text-overflow: ellipsis;
    &::after{
      content: '';
      display: block;
      // background: #FAB47A;
      width: 0;
      height: 0;
      border-width: 20px;
      border-style: solid;
      border-color: #D42E00 transparent transparent transparent;
      position: absolute;
      left: 180px;
      bottom: -38px;
    }
  }
   &.fourTab{
    .footer-item {
      &.redColor::after {
        background: #fff;
        right: 50px;
        background: #f00;
      }
    }
  }
}
.redColor {
  position: relative;
}
.redColor:after{
  position: absolute;
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  background: red;
  top: 5px;
  border-radius: 10px;
  right: 30px;
}
</style>



